.general-chart-design {

    position: absolute;
    height: 100%;
    padding: 0;
    width: 100%;
    background: #fff;
    overflow: hidden;

}


.left-sidebar {
  position: absolute;
  top: 0;
  width: 200px;
  bottom: 0;
  left: 0;
  padding: 0;
  background: #fff;
  border-right: #e8e8e8 1px solid;
  z-index: 99;
}


.nowrap {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}


.center-sidebar {
  width: calc(100% - 473px);
  position: absolute;
  left: 201px;
  top: 0px;
  height: 100%;
  bottom: 0;
  padding: 0 5px;
}


.center-left-sidebar {
  left: 0px;
  width: calc(100% - 272px);
}


.center-right-sidebar {
  width: calc(100% - 212px);
}


.center-all-sidebar {
  left: 0px;
  width: calc(100% - 10px);
}


.chart-data-empty {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #fff;
  z-index: 90;
  text-align: center;
  line-height: 100%;
}


.chart-data-empty span {
  display: block;
  top: 50%;
  position: absolute;
  left: 50%;
  width: 300px;
  margin: -50px 0 0 -150px;
  font-weight: 700;
  color: #555;
  height: 100px;
  line-height: 100px;
  font-size: 13px;
  letter-spacing: 1px;
}


.right-sidebar {
  width: 261px;
  position: absolute;
  right: 0;
  top: 0px;
  bottom: 0px;
  padding: 0px;
  overflow: auto;
  border-left: #e8e8e8 1px solid;
  z-index: 99;
  -webkit-box-shadow: 2px 0 7px 0 rgb(0 0 0 / 6%), inset -1px 0 0 0 rgb(255 255 255 / 20%);
  box-shadow: 2px 0 7px 0 rgb(0 0 0 / 6%), inset -1px 0 0 0 rgb(255 255 255 / 20%);

}


.original-fields-li {
  line-height: 35px;
  height: 35px;
  padding: 0 5px;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  position: relative;
}


.original-fields-li:hover {
  background: #eee;
}


.icon-authority-cms {
  display: inline-block;
  float: left;
}


.original-fields-item-name {
  line-height: 35px;
  display: inline-block;
  float: left;
  height: 35px;
  margin: 0;
  padding: 0 0 0 7px;
  font-size: 10px;
  width: 130px;
  color: #666;
  font-weight: 700;
}


.analysis-bar ul {
  display: block;
}


.analysis-bar>ul>li {
  height: 35px;
  line-height: 35px;
  padding: 2px 5px;
  margin: 3px 0 0 0;
  display: block;
  border-bottom: 1px solid #e8e8e8;
  position: relative;
}


.chart-type-bar {
  margin: 0px 10px 0;
  display: inline-block;
}


.chart-design-box-view {
  height: calc(100% - 140px) !important;
